import {  
    mapMutations  
} from 'vuex';
import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			couponList: [],
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '未使用'
				},
				{
					state: 1,
					text: '已使用'
				},
				{
					state: 2,
					text: '已过期'
				}
			],
			state: 0
		};
	},
	onLoad(option){
		this.mycoloect()
	},
	onPullDownRefresh() {
		this.mycoloect(true)
	},
	methods:{
		mycoloect(flag) {
			uni.showLoading({title: '正在加载'})
			let url = '/coupon/mylist?status='+this.state+'&page=1&limit=100'
			api.httpget(url).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					this.couponList = respons.data.list
				}
				if(flag) uni.stopPullDownRefresh();
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
				if(flag) uni.stopPullDownRefresh();
			})
		},
		//顶部tab点击
		tabClick(index,state){
			this.tabCurrentIndex = index;
			this.state = state
			this.mycoloect()
		},
		//详情
		navToDetailPage(item){
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		}
	}
}
